<template>
    <div class="discount">
        <h4>지금 결제하면 {{ discountPrice }}% 할인</h4>
    </div>
</template>

<script>
export default {
    name: 'TopBanner',
    data() {
        return {
            discountPrice : 30
        }
    },
    mounted() {   
        setInterval(()=>{
            if(this.discountPrice >0) {
                this.discountPrice--;
            } else {
                clearInterval();
            }
        },1000);
    }
}
</script>

