<template>
 
 <div class="black-bg" v-if="openModal == true">
    <div class="white-bg">
      <h4>{{ oneroomData[clickIndex].title }}</h4>
      <img class="room-img" :src="oneroomData[clickIndex].image">
      <p>{{ oneroomData[clickIndex].content }}</p>
      <input v-model="month">
      <p> {{ month }}개월 선택함 : {{ oneroomData[clickIndex].price * month}}원</p>
      <button @click="$emit('closeModal')">닫기</button>
    </div>
  </div>
   
</template>

<script>
export default {
    name : 'ModalPopup',
    data() {
        return {
            month : 1,
        }
    },
    watch : {
      month(param) {
        //isNaN => 숫자가 아니면 true
        if(isNaN(param) == true) {
          alert('숫자만 입력하삼!');
          this.month = 1;
        }
      }
    },
    updated() {
      if(this.month == 2) {
        alert('2는 선택할 수 없음.');
        this.month = 1;
      }
    },
    props : {
        oneroomData : Object,
        clickIndex : Number,
        openModal : Boolean,
    },
}
</script>

