<template>
  <transition name="fade">
    <ModalPopup @closeModal="openModal = false" :oneroomData="oneroomData" :clickIndex="clickIndex" :openModal="openModal"/>
  </transition>

  <div class="menu">
    <a v-for="(v, i) in menus" :key="i">{{ v }}</a>
  </div>

  <TopBanner v-if="showDiscount == true"/>

  <button @click="priceSort">가격순정렬</button>
  <button @click="sortBack">되돌리기</button>

  <RoomCard @openModal="openModal = true; clickIndex = $event" v-for="(v2, i2) in oneroomData" :key="i2" :oneroomData="oneroomData[i2]" />
</template>

<script>
import oneroom from './assets/oneroom';
import TopBanner from './TopBanner.vue';
import ModalPopup from './ModalPopup.vue';
import RoomCard from './RoomCard.vue';

export default {
  name: 'App',
  data() {
    return {
      showDiscount : true,
      oneroomDataOrg : [...oneroom],
      clickIndex : 0,
      oneroomData : oneroom,
      openModal : false,
      reportCount : [0, 0, 0],
      menus : ['Home', 'Shop', 'About'],
    }
  },
  methods: {
    increase(input) {
      this.reportCount[input]++;
    },
    sortBack() {
      this.oneroomData = [...this.oneroomDataOrg];
    }, 
    priceSort() {
      this.oneroomData.sort(function(a,b) {
        return a.price - b.price
      });
    }
  },
  created() {

  },
  mounted() {
    // //function() 대신에 ()=> 를 써야 this변수가 제대로 작동
    // setTimeout(()=>{
    //   this.showDiscount = false;
    // }, 2000);
  },
  components: {
    TopBanner : TopBanner,
    ModalPopup : ModalPopup,
    RoomCard : RoomCard
  }
}
</script>

